.home {
  background-color: #feb729;
}

.recents {
  background-color: #a8b1b8;
  padding: 15px;
  color: black;
  font-size: larger;
  vertical-align: top;
  display: inline-block;
  width: 40%;
  min-height: 100vh;
  max-width: 80%;
}

.recentblog {
  font-size: medium;
  padding-top: 10px;
  padding-bottom: 20px;
}

.boxes {
  background-color: #feb729;
  color: black;
  display: inline-block;
  width: 60%;
  max-width: 80%;
}

.hellobox {
  background-color: #feb729;
  color: black;
  padding: 5px;
  vertical-align: top;
}

.image {
  padding: 5px;
  vertical-align: middle;
  display: inline-block;
  width: 30%;
  margin: 5%;
}

.title {
  font-size: xx-large;
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  margin: 5%;
  margin-left: 0px;
}

.text {
  font-size: medium;
  display: inline-block;
  vertical-align: middle;
  width: 50%;
}
