.navbar {
	background-color: #00275e;
	color: white;
	padding: 10px;
	vertical-align: middle;
}

.navtitle {
	color: white;
	font-weight: bold;
	display: inline-block;
	width: 80%;
}

.navlinks {
	display: inline-block;
}

.navlink:hover {
	font-weight: bold;
	cursor: pointer;
}

.navlink {
	padding-left: 5px;
	padding-right: 20px;
	display: inline-block;
}
